import { gsap } from 'gsap';
import { ScrollSmoother } from 'gsap/ScrollSmoother';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { SplitText } from 'gsap/SplitText';
// import { Observer } from 'gsap/Observer'; 
// import { CustomEase } from 'gsap/CustomEase';
// import { CustomBounce } from 'gsap/CustomBounce';
// import { CustomWiggle } from 'gsap/CustomWiggle';
//* ____________________ [ - (GSAP) plugins] ________________________
gsap.registerPlugin(ScrollTrigger, ScrollSmoother, SplitText);

//* ____________________ [ - ScrollTrigger] ________________________
ScrollTrigger.normalizeScroll(false);
ScrollTrigger.config({ ignoreMobileResize: true });

//* ___________________________[ScrollSmoother] ________________________________
//? speed	  	0.5()  2()
//? smooth	 /  	0.5  2
//? effects	 .effects()	true / false
//? smoothTouch	    - 	0  1; 

export function smoother() {
	ScrollSmoother.create({
		wrapper: '#wrapper',
		content: '#content',
		speed: 1,
		smooth: 1,
		effects: true,
		smoothTouch: 0.1,
	});
}

